                  >
                    {{ $t('Download_file') }}
                  </a>
                </div>
                <div
                  class="my_courses_list_radio_block"
                  v-if="(currentQuestion && currentQuestion.type === 'TYPE_SINGLE') || (currentQuestion && currentQuestion.type === 'TYPE_MULTIPLE')">
                  <div
