                    v-if="currentQuestion && currentQuestion.answers"
                    v-for="answer in currentQuestion.answers">
                    <input :disabled="quizEnded" v-if="currentQuestion.type === 'TYPE_SINGLE'" v-model="selectedAnswer"
                           :value="answer.id"
