    </div>
    <b-modal id="show" hide-footer hide-header centered size="lg">
      <div class="store__product__modal" id="dialog" style="    background: #F7E9F6; color: #C460C0;">
        <button
          @click="$bvModal.hide('show')"
          type="button"
